import MicroModal from 'micromodal';

"use strict"
MicroModal.init({
  disableScroll: true,
  awaitOpenAnimation: true,
  awaitCloseAnimation: true,
});

// Scroll to top
const scrollTopFooterButtonElement = document.querySelector('#scrollToTop');
const scrollTopButtonElement = document.querySelector('#scroll-button');
const heroElementHeight = document.querySelector('.hero').offsetHeight;

scrollTopFooterButtonElement.addEventListener('click', function() {
  window.scrollTo({
    top: 0,
    behavior: "smooth"
  })
})

scrollTopButtonElement.addEventListener('click', function() {
  window.scrollTo({
    top: 0,
    behavior: "smooth"
  })
})

// Scroll progress
const progressBar = document.querySelector("#progress-bar");
let scrollHeight;
let currentScrollPosition;

window.addEventListener("scroll", function() {
  scrollHeight = document.body.clientHeight - window.innerHeight;
  if (!!window.MSInputMethodContext && !!document.documentMode) {
    currentScrollPosition = Math.floor(window.pageYOffset);
  } else {
    currentScrollPosition = Math.floor(window.scrollY);
  }

  const progressPercent = Math.round(currentScrollPosition / (scrollHeight * 0.01))
  progressBar.style.width = progressPercent + '%';

  if (currentScrollPosition > heroElementHeight) {
    scrollTopButtonElement.classList.add('active');
  } else {
    scrollTopButtonElement.classList.remove('active');
  }
})

// FAQ tabs

const tabsHeads = document.querySelectorAll('.faq-tab-head');
const tabsHeadsToggle = document.querySelectorAll('.faq-tab-head__toggle');
const tabsBodies = document.querySelectorAll('.faq-tab-body');

for (let i = 0; i < tabsHeads.length; i++) {
  tabsHeads[i].addEventListener("click", function() {
    if (tabsHeadsToggle[i].classList.contains('active')) {
      tabsHeadsToggle[i].classList.remove('active');

      tabsBodies[i].style.maxHeight = 0 + 'px';

      setTimeout(function() {
        tabsBodies[i].style.display = 'none';
        tabsBodies[i].style.maxHeight = '';
      }, 300)
    } else {
      tabsHeadsToggle[i].classList.add('active');

      tabsBodies[i].style.display = 'block';
      const height = tabsBodies[i].offsetHeight;
      tabsBodies[i].style.maxHeight = 0 + 'px';
      setTimeout(function() {
        tabsBodies[i].style.maxHeight = height + 'px';
      })
    }
  });
}

