// contact-popup
const contactPopupButtonDesktop = document.querySelector('.contact-popup-button.desktop');
const contactPopupButtonMobile = document.querySelector('.contact-popup-button.mobile');
const contactPopup = document.querySelector('.contact-popup');

if (!isMobile(navigator.userAgent||navigator.vendor||window.opera)) {
  contactPopupButtonMobile.style.display = "none";
  
  contactPopupButtonDesktop.addEventListener('click', function() {
    if (contactPopupButtonDesktop.classList.contains('active')) {
      contactPopupButtonDesktop.classList.remove('active');
      contactPopup.classList.remove('active');
      setTimeout(function() {
        contactPopup.style.display = 'none';
      }, 200);
    } else {
      contactPopupButtonDesktop.classList.remove('not-clicked');
      contactPopupButtonDesktop.classList.add('active');
      contactPopup.style.display = 'flex';
      
      setTimeout(function() {
        contactPopup.classList.add('active');
      });
    }
  });
} else {
  contactPopupButtonDesktop.style.display = "none";
}

// Form
function mailSend(form, data) {
  const res = fetch("mail.php", {
    method: 'post',
    headers: {
      "Content-type": 'application/json; charset="utf-8"'
    },
    body: JSON.stringify(data),
  }).then(function (res) {
    if (res.ok) {
      form.reset();
    }
  })
}

const contactSectionForm = document.querySelector(".contact-form form");
const contactSectionFormSuccess = document.querySelector(".contact-form--success");
const contactSectionFormName = document.querySelector(".contact-form form input[type=text]");
const contactSectionFormPhone = document.querySelector(".contact-form form input[type=tel]");

const contactPopupForm = document.querySelector(".contact-popup-form");
const contactPopupFormSuccess = document.querySelector(".contact-popup-form--success");
const contactPopupFormPhone = document.querySelector(".contact-form form input[type=tel]");

contactPopupForm.addEventListener("submit", (e) => {
  e.preventDefault();
  let data = {
    phone: contactPopupFormPhone.value
  }

  if (contactPopupFormPhone.value) {
      fetch("mail.php", {
        method: 'post',
        headers: {
          "Content-type": 'application/json; charset="utf-8"'
        },
        body: JSON.stringify(data),
      }).then(function (res) {
        if (res.ok) {
          contactPopupForm.reset();
          contactPopupForm.style.display = "none"
          contactPopupFormSuccess.style.display = "block";
          setTimeout(function() {
            contactPopupFormSuccess.style.opacity = "1";
          })
        }
      })
    }
})

contactSectionForm.addEventListener("submit", (e) => {
  e.preventDefault();
  let data = {
    name: contactSectionFormName.value,
    phone: contactSectionFormPhone.value
  }

  if (contactSectionFormName.value && contactSectionFormPhone.value) {
      fetch("mail.php", {
        method: 'post',
        headers: {
          "Content-type": 'application/json; charset="utf-8"'
        },
        body: JSON.stringify(data),
      }).then(function (res) {
        if (res.ok) {
          contactSectionForm.reset();
          contactSectionForm.style.display = "none"
          contactSectionFormSuccess.style.display = "block";
          setTimeout(function() {
            contactSectionFormSuccess.style.opacity = "1";
          })
        }
      })
    }
})

// Phone mask
const phoneInputs = document.querySelectorAll('input[type=tel]');

for (let i = 0; i < phoneInputs.length; i++) {
  phoneInputs[i].addEventListener('input', function (e) {
    var value = e.target.value.replace(/(?!^\+)\D/g, '');
    if (value.indexOf('+') !== -1) {
      e.target.value = value.slice(0, 12);
    } else {
      e.target.value = value.slice(0, 11);
    }
  });
}